import request from '@/http/request'

// 
export function login (data) {
  return request({
    url: '/user/login', // 
    method: 'post', // 
    data: {
      userId: data.username,
      userPassword: data.password
    }// 
  })
}

// 
export function register (data) {
  return request({
    url: '/user/register', // 
    method: 'post', // 
    data: {
      userId: data.username,
      userPassword: data.password
    }// 
  })
}

